  --green: hsl(106, 50%, 55%);
  --red: hsl(0, 67%, 50%);
  --orange: hsl(31, 88%, 51%);
  --blue: hsl(201, 75%, 48%);
  --radius: ;
}
.app {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: 'left textContainer';
}
.left {
  grid-area: left;
}
.textContainer {
  grid-area: textContainer;
}
.addBtn {
  background-color: var(--blue);
  font-weight: 500;

  color: rgb(255, 255, 255);
  border: 1px solid transparent;
}
.addBtn:hover {
  background-color: hsl(201, 75%, 61%);
  color: rgb(255, 255, 255);
  box-shadow: rgba(1, 4, 9, 0.75) 0px 1px 2px;
  border-color: transparent;
}
.deleteBtn {
  background-color: var(--red);
  font-weight: 500;

  color: rgb(255, 255, 255);
  border: 1px solid transparent;
}
.deleteBtn:hover {
  background-color: hsl(0, 67%, 68%);
  color: rgb(255, 255, 255);
  box-shadow: rgba(1, 4, 9, 0.75) 0px 1px 2px;
  border-color: transparent;
}
.sendBtn {
  background-color: var(--green);
  font-weight: 500;

  color: rgb(255, 255, 255);
  border: 1px solid transparent;
  margin-left: auto;
  margin-right: 20px;
}
.sendBtn:hover {
  background-color: hsl(106, 50%, 75%);
  color: rgb(255, 255, 255);
  box-shadow: rgba(1, 4, 9, 0.75) 0px 1px 2px;
  border-color: transparent;
}
.row-text {
  padding-left: 8px;
}
input {
  padding: 0px 8px;
  background: rgb(17, 18, 23);
  line-height: 1.57143;
  font-size: 14px;
  color: rgb(204, 204, 220);
  border: 1px solid rgba(204, 204, 220, 0.2);
  position: relative;
  z-index: 0;
  -webkit-box-flex: 1;
  flex-grow: 1;
  border-radius: 2px;
  /* height: 100%;
  width: 100%; */
}
.btn {
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  font-family: Inter, Helvetica, Arial, sans-serif;
  padding: 0px 15px;
  height: 32px;
  line-height: 30px;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 2px;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}
.row {
  margin: 0;
}
.inputContainer,
.row {
  display: flex;
}
.header {
  display: flex;
  flex-direction: row;
}
.alert-unlink {
  color: var(--orange);
}
